@import url(https://fonts.googleapis.com/css?family=Indie+Flower);
@import url(https://fonts.googleapis.com/css?family=Merriweather);
/* Header */
.header {
  padding: 15px 30px;
  position: absolute;
  width: 100%;
  display: inline-block;
  right: 0;
  left: 0;
  z-index: 50;
  overflow-x: hidden;
  top: -85px;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}
.header .nav {
  display: inline-block;
  list-style: none;
  float: none;
}
.header .nav li a {
  margin-right: 1.5em;
  text-align: center;
  font-size: 1.3em;
  border-radius: 0em;
  padding: 0;
}
.header .nav li a:after {
  margin-top: 25px;
  position: absolute;
  left: 0;
  width: 0%;
  height: 2px;
  content: '';
  opacity: 0;
  transition: width 0.3s, opacity 0.2s, transform 0.3s;
}
.header .nav li a:hover::after,
.header .nav li a:focus::after {
  opacity: 1;
  width: 100%;
  text-decoration: none;
}
@media only screen and (min-width: 600px) {
  .header .nav li a {
    font-size: 1em;
    margin-left: 2.5em;
    transition: font-size 3s ease 0s;
  }
}
@media only screen and (min-width: 600px) {
  .header .nav {
    float: right;
  }
}
.header.transparent {
  background: none !important;
  border: none;
  box-shadow: none !important;
}
.header.in {
  top: 0px;
  transition: all 0.6s ease 0s;
}
@media only screen and (min-width: 600px) {
  .header.in {
    top: 0px;
  }
}
.header.out {
  top: -85px;
  transition: all 0.6s ease 0s;
}
@media only screen and (min-width: 600px) {
  .header.out {
    top: -104px;
  }
}
.header.black {
  box-shadow: 0 1px 0 rgba(0, 0, 0, 0.08);
  background: rgba(239, 238, 238, 0.9);
}
.header.black li a:after {
  background: #000;
}
@media only screen and (min-width: 960px) {
  .header.black li a {
    color: #393939;
  }
}
.header.white li a {
  color: #fff;
}
.header.white li a:after {
  background: #fff;
}
.header.white li.active a,
.header.white li a:hover,
.header.white li.active a:hover {
  background-color: inherit;
  color: #fff;
  transition: color 1s ease 0s;
}
.header.black li.active a,
.header.black li a:hover,
.header.black li.active a:hover {
  background-color: inherit;
  color: #000;
  transition: color 1s ease 0s;
}
/* main index */
/* Jumbotrom */
.home.main {
  /* start reading */
}
.home.main .jumbotrom {
  background: url('../../img/city3-min.jpg') center center;
  background-size: cover;
  text-align: center;
  position: relative;
  top: 0;
}
.home.main .jumbotrom .container {
  text-align: center;
  top: 50%;
  position: relative;
  margin: 0 auto;
  transform: translateY(-60%);
}
.home.main .jumbotrom a {
  font-size: 0.857142857142em;
  font-style: italic;
  font-weight: 500;
  color: #fff;
  opacity: 0;
  text-decoration: none;
  cursor: pointer;
  transform: translate(0, 60px);
  transition: all 1s ease 0s;
}
.home.main .jumbotrom h1 {
  opacity: 0;
  font-size: 3em;
  padding: 0;
  margin: 0;
  color: #fff;
}
.home.main .jumbotrom h1 span {
  font-family: 'Indie Flower', cursive;
}
.home.main .jumbotrom h2 {
  font-size: 1.5em;
  color: #fff;
  text-align: center;
  opacity: 0;
  transform: translate(0, 20px);
}
.home.main .jumbotrom.init_1 h1 {
  opacity: 1;
  transition: opacity 0.5s ease 1s;
}
.home.main .jumbotrom.init_2 h2 {
  opacity: 1;
  margin-bottom: 0.5em;
  transition: all 1s ease 0s;
  transform: translate(0px, 0px);
}
.home.main .jumbotrom.init_2 a {
  opacity: 1;
  transform: translate(0px, 0px);
}
.home.main .start_reading {
  padding-top: 1.5em;
}
@media only screen and (min-width: 500px) {
  .home.main .start_reading {
    padding-top: 2.5em;
  }
}
/* main-2 skills */
.skills .col-md-3 {
  height: 165px;
  margin-bottom: 4em;
}
.skills .col-md-3 img {
  transform: translate(30px, 0);
}
.skills .row:nth-child(2) .col-md-3:nth-child(1) img {
  margin: 2.5px 0;
}
.skills .row:nth-child(2) .col-md-3:nth-child(2) img {
  margin: 30px 0;
}
.skills .row:nth-child(2) .col-md-3:nth-child(3) img {
  margin: 7.5px 0;
}
.skills .row:nth-child(5) .col-md-3:nth-child(1) img {
  margin: 22.5px 0;
}
.skills .row:nth-child(5) .col-md-3:nth-child(4) img {
  margin: 3.5px 0;
}
.skills .row:nth-child(8) .col-md-3:nth-child(2) img {
  margin: 12.5px 0;
}
.skills .row:nth-child(8) .col-md-3:nth-child(3) img {
  margin: 22px 0;
}
.skills h2 {
  font-family: 'Indie Flower', cursive;
  padding: 30px;
  border-bottom: 1px solid #000;
  border-top: 1px solid #000;
  opacity: 0;
  font-size: 3em;
  padding: 0;
  margin: 0 0 1.5em 0;
  transform: translate(0, 20px);
}
.skills.init_2 h2 {
  opacity: 1;
  transition: all 1s ease 0s;
  transform: translate(0px, 0px);
}
.skills.init_2 .row .col-md-3 img {
  transition: all 1s ease 0s;
  transform: translate(0px, 0px);
}
/*main-2 projects*/
.projects .row {
  margin-top: 30px;
}
.projects a {
  display: block;
}
.projects a:hover {
  opacity: 1;
  background-color: rgba(255, 255, 255, 0.8);
  transform: scale(1);
}
.projects a:hover img {
  opacity: .2;
}
.projects a:hover div {
  opacity: 1;
  transform: scale(1);
}
.projects .descr {
  position: absolute;
  top: 0;
  padding: 20px;
  opacity: 0;
  color: #204056;
  text-align: left;
  line-height: 2;
  transition: transform 0.15s ease-in-out, opacity 0.15s ease-in-out;
  transform-origin: 50% 50%;
  transform: scale(0.95);
}
/* Footer */
.footer {
  padding-top: 2.5em;
  background: #efeeee;
  border-top: 1px solid rgba(0, 0, 0, 0.05);
}
.footer ul {
  list-style: none;
  margin: 0;
  padding: 0;
  float: left;
}
.footer ul li {
  display: inline-block;
  margin: 0 40px 0 0;
}
.footer p {
  text-align: right;
}
.projects .footer {
  margin-top: 3em;
}
html,
body {
  margin: 0;
  padding: 0;
  font-family: 'Merriweather', serif;
}
body {
  background-color: #efeeee;
  font-size: 100%;
  line-height: 1.75em;
  color: #393939;
  font-weight: 500;
  text-align: center;
  overflow: hidden !important;
  -webkit-overflow-scrolling: touch;
}
@media screen and (min-width: 600px) {
  body {
    font-size: 150%;
  }
}
p {
  margin-bottom: 1.00em;
  margin-top: 0;
  line-height: 1.428572em;
  text-align: left;
  font-size: 1.4em;
}
@media screen and (min-width: 700px) {
  p {
    width: 580px;
    margin-left: auto;
    margin-right: auto;
  }
}
.main {
  margin: 0px;
  width: 100%;
  height: auto;
  overflow: auto;
  display: block;
  position: relative;
}
.skills,
.projects {
  padding: 8em 0 0 0;
}
@media only screen and (min-width: 600px) {
  .skills,
  .projects {
    padding: 5em 0 0 0;
  }
}
